/*
$layout-tight: $layout-base / $ratio; // tight - 0.666666667rem / 10.666666672px
$layout-medium: $ratio * $layout-base; // natural - 1.5rem / 24px
$layout-large: $ratio * $layout-medium; // loose - 2.25rem / 36px
$layout-x-large: $ratio * $layout-large; // luxury - 3.375rem / 54px
$layout-xx-large: $ratio * $layout-x-large; // oversized - 5.0625rem / 81px
$layout-xxx-large: $ratio * $layout-xx-large; // supersized - 7.59375rem / 121.5
*/
/*
 * To set up a Montere or Ratio-driven-design grid, all you need are:
 * - The max width of the container
 * - the padding to use for the left and right
 * - the guttering size between columns
 *
 * These are setup with the variables and map below.
*/
/* Set the column Base of the grid. e.g. Base 12 (12 cols), Base 10 (10 col). Used for calculations of column widths
 * Used when you call @mixin col();
 * e.g. @include col(2, $column-base: 10); - says, I want this element to be 2 columns wide, spread across 10 (base) columns grid track.
 * e.g. @include col(2); - says, I want this element to be 2 columns wide, spread across 12 (base) columns grid track. (Note $column-base not passed as argument as it is default at 12).
 */
/* Container max width - in Pixels (it will be converted to Rems automatically)
 * Here we set the max width we want this site to figuratively 'stretch' to.
 * Once it reaches this width, it will not go beyond, regardless of ratio driven design in place.
*/
/* Map of grid settings to apply at different breakpoints.
 *
 * Each key corresponds to the breakpoint naming convention.
*/
/*
  * Container of the grid system - usually the content.
  *
  * usage:
  *  @include container(); - Default.
  *  @include container(8); - Spans 8 columns of '12'.
  *
  * (optional) param: $max-width
  * Used to create a unique layout-container with its own max-width, not the default.
  *
  * (optional) param: $narrow-layout
  * Used with the .layout-container-narrow class to give specific width and padding dimensions
  * which are used for the article container according to the designs.
*/
/*
  * Row mixin - Parent container for the columns.
  *
  * usage:
  *  @include row().
*/
/*
  * Column span - how wide the element should be.
  *
  * usage:
  *  @include col(8); - Width: 8 cols & 7 gutters.
  *  @include col(4, 4) - Width: 4 columns & 4 gutters.
  *
  * (optional) param: $component-layout
  * Used for handling rearranging layout of columns. e.g. from quarters to halves.
  * Usage:
  * @include col(6, $component-layout: 'halves'); - make the column 6 cols in width (50%). and stack in two columns
  *
  * (optional) param: $column-base
  * Used for changing the grid column base from default 12 cols, to anything you like, e.g 10 cols,
  * Useful for unique layouts
  * Usage:
  * @include col(2, $column-base: 10); Gives us a column of width 2 cols, on a base of 10,
  * meaning there would be 8 cols more available in this row.
  *
  * (optional) param: $use-gutter
  * Gives to option to remove the gutter and have columns butt up against each other for a tight layout.
  * Default is to have gutters, but we can turn them off.
  * Usage:
  * @include col(6, $use-gutter: false); Switches off the gutter (margin-right) for this column.
  *
*/
/*
  * Skip - how many columns we want the element to skip.
  *
  * usage:
  *  @include skip(3); - Skips 3 columns (and 3 gutters);
  *  @include skip(3, 2); - Skips 3 columns and 2 gutters);
  *  @include skip(1, 0); - Skips 1 column and 0 gutters);
*/
/*
NOTE:
HOW TO USE THESE BREAKPOINTS MIXINS:
When passing the breakpoint variable into these three mixins,
you must pass it through as a pixel value. That means use the px() function.
e.g. @include from-breakpoint( px($breakpoint-7) ) {}
*/
/*
  * $vpos: the vertical position of the bar (top | bottom)
  * $hpos: the horizontal position of the bar (left | right)
  * $color: background colour of the bar.
  * $width: width of the bar.
  * $outset: If you want the bar to be outset on the element with negative margin, set to true (true | false)
