/* TYPOGRAPHY EXPANSION */
html {
  font-size: 87.5%;
  /* Typically 14px in a lot of web browsers */
}

@media only screen and (min-width: 600px) {
  html {
    font-size: 100%;
    /* Typically 16px in a lot of web browsers */
  }
}


.hidden {
  display: none;
}


.text-center {
  text-align: center;
}


.text-lockup * {
  max-width: 45rem;
  width: 100%;
}


.text-lockup--large * {
  max-width: 55rem;
}

.section__row {
  position: relative;
  display: block;
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  flex-direction: column;
}

@media only screen and (min-width: 840px) {
  .section__row {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
  }
}

.section__row--nowrap {
  flex-wrap: nowrap;
}

.section__row__col {
  position: relative;
  display: block;
  max-height: 100%;
  overflow: hidden;
}

.section__row--one-col .section__col {
  flex: 0 0 100%;
}

@media only screen and (min-width: 840px) {
  .section__row--two-col .section__col {
    flex: 1 0 50%;
  }
}

@media only screen and (min-width: 840px) and (-ms-high-contrast: active), only screen and (min-width: 840px) and (-ms-high-contrast: none) {
  .section__row--two-col .section__col {
    flex: 0 50%;
    flex-basis: auto;
    width: 50%;
  }
}

@media only screen and (min-width: 840px) {
  .section__row--three-col .section__col {
    flex: 1 0 33.33%;
  }
}

@media only screen and (min-width: 840px) and (-ms-high-contrast: active), only screen and (min-width: 840px) and (-ms-high-contrast: none) {
  .section__row--three-col .section__col {
    flex: 0 33.33%;
    flex-basis: auto;
    width: 33.33%;
  }
}

