.site-btn {
  background-color: #d16527;
  color: #ededed;
  font-family: "Chakra Petch Bold", "Chakra Petch Regular", "Arial", sans-serif;
  font-size: 1.125em;
  text-transform: uppercase;
  line-height: 1.125;
  padding: 14px 60px;
  border-radius: 0;
  border: 2px solid #d16527;
  clip-path: polygon(20px 0, 100% 0, 100% calc(100% - 20px), calc(100% - 20px) 100%, 0 100%, 0 20px);
}


.site-btn:hover,
.site-btn:focus,
.site-btn:active,
.site-btn:focus-within {
  text-decoration: none;
}


.site-btn:focus,
.site-btn:active {
  outline: 5px auto -webkit-focus-ring-color;
}

.webform-submission-subscription-form-form .form-type-email label {
  display: block;
  margin-bottom: 0.9375rem;
  color: #ededed;
}
